import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainComponent }      from './main/main.component';
import { CvComponent }      from './cv/cv.component';
import { BlogComponent }      from './blog/blog.component';
import { DemoGlobeComponent }      from './demo-globe/demo-globe.component';
import { DemoPainterComponent }      from './demo-painter/demo-painter.component';

const routes: Routes = [
  { path: '', redirectTo: 'main', pathMatch: 'full' },
  { path: 'main', component: MainComponent },
  { path: 'cv', component: CvComponent },
  { path: 'blog', component: BlogComponent },
  { path: 'globe', component: DemoGlobeComponent },
  { path: 'painter', component: DemoPainterComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
